import BaseApiService from "../BaseApiService";

const DEFAULT_PATH = "/communication/api/user";

class CommunicationService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const endPoint = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
  getAllNotifications = (start = 0, limit = 10, search = "") => {
    const endPoint = `${DEFAULT_PATH}/notification/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
  updateNotificationStatusToRead = () => {
    const endPoint = `${DEFAULT_PATH}/notification/mark/as/read`;
    return this.makePutRequestWithAuth(endPoint, {});
  };
}

export default new CommunicationService();
